.approximate-color-item{display:flex;flex-direction:column;justify-content:space-between;align-items:center;width:calc(100% / 41);height:96px;padding:4px 0;border:1px dotted #d3d3d3;font-size:1.0rem;cursor:pointer;transition:all 500ms ease}@media screen and (max-width: 1024px){.approximate-color-item{font-size:0.8rem}}@media screen and (max-width: 768px){.approximate-color-item{font-size:0.6rem}}@media screen and (max-width: 414px){.approximate-color-item{font-size:0.4rem}}.approximate-color-item:hover{opacity:.9}.approximate-color-item--white{color:#fff}.approximate-color-item--black{color:#000}

.base-color{width:100%;border-top:1px solid #030303;border-right:1px solid #030303;border-bottom:1px solid #030303}

.complementary-color-area{margin-top:16px;padding:16px 32px;border-radius:16px}.complementary-color-area__description{margin:4px 0}

.hex-text-field{display:inline-block}.hex-text-field__label{margin-right:4px;font-size:1.4rem}@media screen and (max-width: 414px){.hex-text-field__label{margin-right:2px;font-size:1.2rem}}.hex-text-field__input{padding:4px 8px;width:96px;height:32px;border-radius:4px;box-shadow:2px 2px 4px #d3d3d3;background-color:#fff;color:#000;font-weight:normal}@media screen and (max-width: 414px){.hex-text-field__input{padding:2px 4px;width:52px}}

.palette-label{display:flex;justify-content:center;align-items:center;width:32px;border-right:1px solid #030303;border-left:1px solid #030303;background-color:#d3d3d3;color:#000}

@media screen and (max-width: 414px){.rgb-text-field__label{font-size:0.8rem}}.rgb-text-field__input--red{width:48px;height:32px;margin-right:16px;padding:4px 8px;border-radius:4px;box-shadow:2px 2px 4px #d3d3d3;background-color:#fff;font-weight:normal;color:red}@media screen and (max-width: 414px){.rgb-text-field__input--red{width:40px}}.rgb-text-field__input--green{width:48px;height:32px;margin-right:16px;padding:4px 8px;border-radius:4px;box-shadow:2px 2px 4px #d3d3d3;background-color:#fff;font-weight:normal;color:lime}@media screen and (max-width: 414px){.rgb-text-field__input--green{width:40px}}.rgb-text-field__input--blue{width:48px;height:32px;margin-right:16px;padding:4px 8px;border-radius:4px;box-shadow:2px 2px 4px #d3d3d3;background-color:#fff;font-weight:normal;color:blue}@media screen and (max-width: 414px){.rgb-text-field__input--blue{width:40px}}

